(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension', ['ngRoute', 'ui.bootstrap', 'ngYoutubeEmbed']);

})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .directive('navbarHeader', navbarHeader);

  /** @ngInject */
  function navbarHeader() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {},
      controller: NavbarController,
      controllerAs: 'nc',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($uibModal) {
      var vm = this;
      vm.openLoginForm = function () {
        $uibModal.open({
           animation: true,
           templateUrl: 'app/components/login/login.html',
           controller: 'LoginController',
           controllerAs: 'lc',
           resolve: {
             items: function () {
               return [];
             }
           }
        });
      };
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('MenuController', MenuController);

  /** @ngInject */
  function MenuController(GENRES) {
    var vm = this;
    vm.genreKeys = Object.keys(GENRES);
    vm.selectedGenre = GENRES['pop'];

    vm.setGenre = function(key) {
      vm.selectedGenre = GENRES[key];
    };

  }
})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($scope, $uibModalInstance, $log) {
    var vm = this;
    vm.login =  function () {
      $log.info("login clicked");
      $uibModalInstance.close();
    };

    vm.cancel = function () {
      $log.info("Cancel clicked");
      $uibModalInstance.dismiss('cancel');
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('DetailController', DetailController);

  /** @ngInject */
  function DetailController(SelectedSong) {
      var vm = this;
      vm.selectedSong = SelectedSong[0];
  }
})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController() {
  }
})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .config(routeConfig);

  function routeConfig($routeProvider, $locationProvider) {
    $routeProvider
      .when('/songs/:GENRE/:ID/', {
        templateUrl: 'app/components/detail/detail.html',
        controller: 'DetailController',
        controllerAs: 'detail',
        resolve: {
          SelectedSong: function($route, $filter, GENRES) {
            return $filter('filter')(GENRES[$route.current.params.GENRE], { id: $route.current.params.ID });
          }
        }
      })
      .when('/', {
        templateUrl: 'app/components/menu/menu.html',
        controller: 'MenuController',
        controllerAs: 'menu'
      })
      .otherwise({
        redirectTo: '/'
      });
    $locationProvider.html5Mode(true);
  }

})();

(function() {
  'use strict';

  var GENRES = {
    'pop'  : [{id:1, genre:'pop', image:'http://lorempixel.com/' + 501 + '/250', artist:'Jason Mraz', title:'I wont give up', lyricVideo:'https://www.youtube.com/watch?v=TdN5GyTl8K0', musicVideo:'', taskUrl: '/tasks/1.png'}],
    'rock' : [{id:1, genre:'rock', image:'http://lorempixel.com/' + 501 + '/250', artist:'Nickleback', title:'If Everyone Cared', lyricVideo:'https://www.youtube.com/watch?v=CkliFcBLXc8', musicVideo:'', taskUrl: ''},
              {id:2, genre:'rock', image:'http://lorempixel.com/' + 502 + '/250', artist:'Foo Fighters', title:'Breakout', lyricVideo:'https://www.youtube.com/watch?v=CkliFcBLXc8', musicVideo:'', taskUrl: ''}],
    'theatre' : [{id:1, genre:'theatre', image:'http://lorempixel.com/' + 501 + '/250', artist:'Wicked', title:'Defying Gravity', lyricVideo:'https://www.youtube.com/watch?v=CkliFcBLXc8', musicVideo:'', taskUrl: ''}]
  };

  angular
    .module('jukeboxcomprehension')
    .constant('GENRES', GENRES );
})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .config(config);

  /** @ngInject */
  function config($logProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib

  }

})();

